#map-wrapper{position:fixed;top:100px;left:0;right:0;bottom:0}#map-wrapper div#map{margin:0 auto;max-width:600px;height:100%;display:block;background:#c0c0c0}#map-wrapper div#map .myInfoWindow{background:#fff}#map-wrapper div#map .myInfoWindow .date{font-size:110%;font-weight:bold}#map-wrapper div#map .myInfoWindow .fromnow{color:red;font-weight:bold}#map-wrapper .loader{position:absolute;top:45%;left:47%;z-index:999999999}#header{background:#f7f7f7;height:100px}#header #wrapper{max-width:600px;margin:0 auto;padding:10px}#header #wrapper .branding{display:block;color:#27ab46;font-size:120%}#header #wrapper .place-input{margin-top:6px;top:30px;left:10%}#header #wrapper .place-input input{font-size:110%;min-width:70%}#header #wrapper .place-input .current{border:1px solid #c0c0c0;background:#fff;height:23px;padding:2px;display:inline-block;border-radius:3px;vertical-align:center;cursor:pointer}#header #wrapper .place-input .current img{width:21px;vertical-align:middle;margin-bottom:5px;display:inline}#header #wrapper .place-input .current:hover{background:#f0f0f0}#notify{margin-top:-25px;background:#ffcbcb}#notify .content{padding:3px 0;margin:0 auto;max-width:600px;text-align:center}#footer{position:fixed;top:4px;right:10px;font-size:13px;color:#ccc}



